import { Observable } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { IPaymentMethodListVM, IPaymentMethodsVM } from "app/dashboard/models/setup/financial-setup/payment-methods";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class PaymentMethodService {
  constructor(private http: HttpClient) {}

  getAllPaymentMethods(page:number,EduCompId:number): Observable<IPaymentMethodListVM> {
    return this.http.get<IPaymentMethodListVM>(
      `${environment.baseUrl}PaymentMethod/getPaymentMethodByPage/${EduCompId}/${page}`
    );
  }
  getAllPaymentMethodsDropdown(EduCompId:number) {
    return this.http.get(`${environment.baseUrl}PaymentMethod/getAllPaymentMethod/${EduCompId}`);
  }

  getPaymentMethodById(id: any) {
    return this.http.get(
      `${environment.baseUrl}SchoolSystem/PaymentMethods/${id}`
    );
  }

  addEditPaymentMethod(data: any) {
    return this.http.post(
      `${environment.baseUrl}PaymentMethod/addEditPaymentMethod`,
      data
    );
  }


  deletePaymentMethod(id: any) {
    return this.http.delete(
      `${environment.baseUrl}PaymentMethod/deletePaymentMethod/${id}`
    );
  }
}
