import { TranslateService } from "@ngx-translate/core";
import { Component, Input, OnInit } from "@angular/core";
import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";
import { FormControl } from "@angular/forms";
import { IEducationYearDropDown } from "app/dashboard/models/setup/educational-setup/education-year";

@Component({
  selector: "educational-year-by-stage-dropdown",
  templateUrl: "./educational-year-dropdown.component.html",
  styleUrls: ["./educational-year-dropdown.component.scss"],
})
export class EducationalYearByStageDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() stageControl: FormControl;
  @Input() submitted: boolean;
  EducationYears: IEducationYearDropDown[];
  curLang: string;

  constructor(
    private educationYearService: EducationYearService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.control.disable();

    this.stageControl.valueChanges.subscribe((stageId) => {
      this.control.setValue(null);
      if (stageId > 0) {
        this.getEducationalYearDropdown(stageId);
        this.control.enable();
      }else{
        this.control.disable();
      }
      console.log("Year-Dropdown-stageControl", stageId);
    });
  }

  getEducationalYearDropdown(stageId: number) {
    this.educationYearService
      .getEducationalYearByStageDropdown(stageId)
      .subscribe((res) => {
        if (res) {
          this.EducationYears = res.eduYearList;
        }
      });
  }
}
