import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IEduTableGroupByDay,
  IWeeklyLectureVM,
} from "app/dashboard/models/educational-tabels/weekly-table";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GroupByService } from "app/shared/services/General/group-by.service";

@Component({
  selector: "app-table-calender",
  templateUrl: "./table-calender.component.html",
  styleUrls: ["./table-calender.component.scss"],
})
export class TableCalenderComponent implements OnInit {
  @Input("classId") classId: number = 0;

  curLang: string;
  EduCompId: number;

  functionId: number;
  dayList: IEduTableGroupByDay[];
  maxLacInDay: IWeeklyLectureVM[] = [];

  constructor(
    private translate: TranslateService,
    private route: ActivatedRoute,
    public authService: AuthService,
    private WeeklyTableServ: WeeklyTableService,
    private GroupByService: GroupByService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {}

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });

    if (this.classId > 0) this.getTableDetail();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.classId.currentValue > 0) this.getTableDetail();
    else if (this.classId == 0) {
      this.maxLacInDay = [];
      this.dayList = [];
    }
  }

  // Return table
  getTableDetail() {
    this.maxLacInDay = [];
    this.WeeklyTableServ.getEduTableByClassId(this.classId).subscribe((res) => {
      if (res) {
        // group lectures with their day
        this.dayList = this.GroupByService.groupBy(res, "weeklyDayId").sort(
          (day1, day2) => day1.key - day2.key
        );
        // sort lectures in each day
        this.dayList.map((day) => {
          return {
            key: day.key,
            List: day.List.sort(
              (lec1, lec2) => lec1.Weekly_Lec_ID - lec2.Weekly_Lec_ID
            ),
          };
        });

        // get max no of lectures in this class > from return list > to show in table header
        this.dayList.forEach((day) => {
          if (day.List.length > this.maxLacInDay.length) {
            this.maxLacInDay = day.List.map(
              ({ Weekly_Lec_ID, weeklyLec_Ar_Name, weeklyLec_En_Name }) => ({
                id: Weekly_Lec_ID,
                Ar_Name: weeklyLec_Ar_Name,
                En_Name: weeklyLec_En_Name,
              })
            );
          }
        });
      }
    });
  }
}
