import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  CourseAddEditVM,
  CourseGetResponseVM,
  CourseGetVM,
  CourseGroupAddEditVM,
  CourseGroupGetResponseVM,
  CourseGroupGetVM,
} from "app/dashboard/models/setup/educational-setup/course";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class CourseService {
  constructor(private http: HttpClient) {}

  // Return All  Courses by page
  getAllCourses(page: number) {
    return this.http.get<CourseGetResponseVM>(
      `${environment.baseUrl}Course/getCourseByPage/${page}`
    );
  }

  // Return All  Courses by page, eduCopmId
  getAllCoursesByEduCopmIdPage(eduCopmId: number, page: number) {
    return this.http.get<CourseGetResponseVM>(
      `${environment.baseUrl}Course/getCourseByEduCompIdByPage/${eduCopmId}/${page}`
    );
  }

  // Return All Courses > drop down
  getAllCoursesDropDown() {
    return this.http.get<CourseGetVM[]>(
      `${environment.baseUrl}Course/getAllCourse`
    );
  }

  getCoursesByYearDropDown(yearId: number) {
    return this.http.get<{ subjectList: CourseGetVM[] }>(
      `${environment.baseUrl}Course/getCourseByEduYearId/${yearId}`
    );
  }

  // getCoursesByCourseGroup(groupId: number) {
  //   return this.http.get(
  //     `${environment.baseUrl}Course/getCoursesByGroupId/${groupId}`
  //   );
  // }

  // Return Course by id
  getCourseById(id: number) {
    return this.http.get<CourseGetVM[]>(
      `${environment.baseUrl}Course/getAllCourseById/${id}`
    );
  }

  // Add New Course
  addEditCourse(data: CourseAddEditVM) {
    return this.http.post(`${environment.baseUrl}Course/addEditCourse`, data);
  }

  // Delete Course
  deleteCourse(id: number) {
    return this.http.delete(`${environment.baseUrl}Course/deleteCourse/${id}`);
  }

  // Return All  Course Groups by page
  getAllCourseGroups(page: number) {
    return this.http.get<CourseGroupGetResponseVM>(
      `${environment.baseUrl}CourseGroup/getCourseGroupByPage/${page}`
    );
  }

  getAllCourseGroupsDropDown() {
    return this.http.get<CourseGroupGetVM[]>(
      `${environment.baseUrl}CourseGroup/getAllCourseGroup`
    );
  }

  uploadSubjectSyllabus(body: any) {
    return this.http.post(`${environment.baseUrl}upload-subject-syllabus`, body);
  }

  // Add New Course Group
  addEditCourseGroup(data: CourseGroupAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}CourseGroup/addEditCourseGroup`,
      data
    );
  }

  // Delete Course Group
  deleteCourseGroup(id: number) {
    return this.http.delete(
      `${environment.baseUrl}CourseGroup/deleteCourseGroup/${id}`
    );
  }
}
