import { AuthService } from "app/shared/services/auth/auth.service";
import { IsAdminGuard } from "app/shared/services/auth/Guards/roles/is-admin.guard";
import { NgModule } from "@angular/core";
import {
  RouterModule,
  Routes,
  PreloadAllModules,
  Router,
} from "@angular/router";
import { AdminHomeComponent } from "./home/pages/admin-home/admin-home.component";
import { HomeIndexComponent } from "./home/pages/home-index/home-index.component";

import { FullLayoutComponent } from "./layouts/full-layout.component";

import { Full_ROUTES } from "./shared/routes/full-layout.routes";
import { NotUserGuard } from "./shared/services/auth/Guards/auth/not-user.guard";
import { IsUserGuard } from "./shared/services/auth/Guards/auth/is-user.guard";
import { HttpClient } from "@angular/common/http";
const appRoutes: Routes = [
  {
    path: "",
    redirectTo: "home",
    pathMatch: "full",
  },

  // {
  //   path: "",
  //   redirectTo: "landing",
  //   pathMatch: "full",
  // },

  {
    path: "home",
    component: HomeIndexComponent,
    canActivate: [NotUserGuard],
  },

  {
    path: "",
    component: FullLayoutComponent,
    data: { title: "full Views" },
    children: Full_ROUTES,
    //  canActivate: [AuthGuard],
  },

  {
    path: "auth",
    loadChildren: () =>
      import("./auth/registration.module").then((m) => m.RegistrationModule),
  },

  {
    path: "**",
    redirectTo: "auth/error",
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes, {
      preloadingStrategy: PreloadAllModules,
      relativeLinkResolution: "legacy",
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
