import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";

@Component({
  selector: "cources-by-year-dropdown",
  templateUrl: "./cources-dropdown.component.html",
  styleUrls: ["./cources-dropdown.component.scss"],
})
export class CourcesByYearDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() yearControl: FormControl;
  @Input() submitted: boolean;
  @Input() multiple: boolean = false;
  Courses: CourseGetVM[];
  curLang: string;

  constructor(
    private courseService: CourseService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.control.disable();

    this.yearControl.valueChanges.subscribe((yearId) => {
      this.control.setValue(null);
      if (yearId > 0) {
        this.getCoursesByYear(yearId);
        this.control.enable();
      } else this.control.setValue(null);
      console.log("Cources-Dropdown-yearId", yearId);
    });
  }

  getCoursesByYear(yearId: number) {
    this.courseService.getCoursesByYearDropDown(yearId).subscribe((res) => {
      if (res) this.Courses = res.subjectList;
    });
  }
}
