import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IUnitVM } from "app/dashboard/models/setup/educational-setup/cource-structure";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";

@Component({
  selector: "units-by-course-dropdown",
  templateUrl: "./units-dropdown.component.html",
  styleUrls: ["./units-dropdown.component.scss"],
})
export class UnitsByCourseDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() courceControl: FormControl;
  @Input() submitted: boolean;
  units: IUnitVM[];
  // units = [
  //   { ar_name: "1", id: "1" },
  //   { ar_name: "2", id: "2" },
  //   { ar_name: "3", id: "3" },
  // ];
  constructor(private courceStructureService: CourceStructureService) {}

  ngOnInit(): void {
    this.control.disable();

    this.courceControl.valueChanges.subscribe((courceId) => {
      if (courceId > 0) {
        this.getUnits(courceId);
      } else this.control.setValue(null);
      console.log("Units-Dropdown-courceId", courceId);
    });
    this.control.valueChanges.subscribe((unitID) => {
       console.log("Units-Dropdown-unitID", unitID);
    });
  }

  getUnits(courceId: number) {
    this.courceStructureService
      .getUnits(courceId)
      .subscribe((res: IUnitVM[]) => {
        if (res) this.units = res;
      });
  }
}
