import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import {
  IAddEditEducationYear,
  IEducationYear,
  IEducationYearDropDown,
  IEducationYearList,
} from "app/dashboard/models/setup/educational-setup/education-year";

@Injectable({
  providedIn: "root",
})
export class EducationYearService {
  constructor(private http: HttpClient) {}

  // Return All Educational Years
  getAllEducationalYears(page: number) {
    return this.http.get<IEducationYearList>(
      `${environment.baseUrl}EducationalYear/getEducationalYear/${page}`
    );
  }

  // get all educational years
  getEducationalYearDropdown() {
    return this.http.get<{ eduYearList: IEducationYearDropDown[] }>(
      `${environment.baseUrl}EducationalYear/getAllEducationalYear`
    );
  }

  // get all educational years by stage id
  getEducationalYearByStageDropdown(stageId: number) {
    return this.http.get<{ eduYearList: IEducationYearDropDown[] }>(
      `${environment.baseUrl}EducationalYear/getEducationalYearByStageId/${stageId}`
    );
  }

  // get all educational years by stage id
  getEduYearWithCurrentClassroomId(classId: number) {
    return this.http.get<IEducationYearDropDown>(
      `${environment.baseUrl}EducationalYear/getEduYearWithCurrentClassroomId/${classId}`
    );
  }

  //Add - edit Educational Year
  addEditEducationalYear(data: IAddEditEducationYear) {
    return this.http.post(
      `${environment.baseUrl}EducationalYear/addEditEducationalYear`,
      data
    );
  }

  //Delete Educational Year
  deleteEducationalYear(EducationalYearId: number) {
    return this.http.delete(
      `${environment.baseUrl}EducationalYear/RemoveEducationalYear/${EducationalYearId}`
    );
  }
}
