import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  GovernorateAddEditVM,
  GovernorateGetResponseVM,
  GovernorateGetVM,
} from "app/dashboard/models/setup/general-setup/location/governorate";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class GovernorateService {
  constructor(private http: HttpClient) {}

  // Return All  Governorates by page
  getAllGovernorates(page: number) {
    return this.http.get<GovernorateGetResponseVM>(
      `${environment.baseUrl}Governorate/getGovernorateByPage/${page}`
    );
  }

  // Return All  Governorates > dropDown list
  getAllGovernoratesDropDown() {
    return this.http.get<GovernorateGetVM[]>(
      `${environment.baseUrl}Governorate/getAllGovernorate`
    );
  }

  //Add New Governorate
  addEditGovernorate(data: GovernorateAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}Governorate/addEditGovernorate`,
      data
    );
  }

  //Delete Governorate
  deleteGovernorate(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Governorate/deleteGovernorate/${id}`
    );
  }
}
