import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import {
  IApplicationGetResponseVM,
  IBloodType,
  IChangeStatusVM,
  IGuardianRelation,
  IrejectionReasonVM,
  IStatusVM,
  IStudentInfoStatusVM,
  IStudentStatus,
} from "app/dashboard/models/setup/student/application";

@Injectable({
  providedIn: "root",
})
export class ApplicationService {
  constructor(private http: HttpClient) {}

  isPreUniversityStudent(eduCompId: number) {
    return this.http.get<boolean>(
      `${environment.baseUrl}AJAX/GetStatusOfParentMandatory/${eduCompId}`
    );
  }

  // get all blood types
  getAllBloodTypes() {
    return this.http.get<IBloodType[]>(
      `${environment.baseUrl}AJAX/GetBloodTypes`
    );
  }

  // get all blood types
  getAllStudentStatus() {
    return this.http.get<{ StatusList: IStudentStatus[] }>(
      `${environment.baseUrl}Status/GetStudentYearStatus`
    );
  }

  // get Guardian relations with student
  getAllGuardianRelations() {
    return this.http.get<IGuardianRelation[]>(
      `${environment.baseUrl}AJAX/GetGuardianTypes`
    );
  }

  //#region studentInfo
  // Return All Applicants by page
  getAllApplicants(page: number, eduCompId: number) {
    return this.http.post<IApplicationGetResponseVM>(
      `${environment.baseUrl}Studnt/SearchStudentAppStatus`,
      {
        // StatusName: "", // to use as search by status
        page: page,
        eduCompId: eduCompId,
      }
    );
  }

  // Delete Application
  deleteApplicant(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Student/deleteStudentApp/${id}`
    );
  }
  //#region

  //#region status
  getAllApplicationStatus() {
    return this.http.get<IStatusVM[]>(
      `${environment.baseUrl}Status/GetAllStatus`
    );
  }

  getApplicantStatusInfoById(statusId: number) {
    return this.http.get<IStudentInfoStatusVM>(
      `${environment.baseUrl}Status/GetApplicationStatusById/${statusId}`
    );
  }

  getAllRejectionReasons() {
    return this.http.get<IrejectionReasonVM[]>(
      `${environment.baseUrl}Student/GetAllRejectionReasons`
    );
  }

  // change status of applicant
  changeStatus(data: IChangeStatusVM) {
    return this.http.post(
      `${environment.baseUrl}Studnt/ChangeStatusStudent`,
      data
    );
  }
  //#region
}
