import { NgxSpinnerService } from "ngx-spinner";
import { TranslateService } from "@ngx-translate/core";
import { AuthService } from "app/shared/services/auth/auth.service";
import {
  Component,
  OnInit,
  ChangeDetectorRef,
  AfterViewInit,
  OnDestroy,
} from "@angular/core";
import { ConfigService } from "../services/config.service";
import { Subscription } from "rxjs";
import { GeneralService } from "../services/General/general.service";
import {
  IBranchesVM,
  IEducationalCompsVM,
  statutsDropVM,
} from "../models/general/general";
import { Router } from "@angular/router";
import { UserFunctionsService } from "../services/auth/user-functions.service";

@Component({
  selector: "app-horizontal-menu",
  templateUrl: "./horizontal-menu.component.html",
  styleUrls: ["./horizontal-menu.component.scss"],
})
export class HorizontalMenuComponent
  implements OnInit, AfterViewInit, OnDestroy
{
  public menuItems: any[];
  public config: any = {};
  level: number = 0;
  transparentBGClass = "";
  menuPosition = "Top";

  layoutSub: Subscription;

  EducationalComps: IEducationalCompsVM[];
  Branches: IBranchesVM[];
  EduCompId: any;
  branchId: any;
  constructor(
    public router: Router,
    public authService: AuthService,
    public userFunctionsService: UserFunctionsService,
    private configService: ConfigService,
    private cdr: ChangeDetectorRef,
    private translate: TranslateService,
    private GeneralServ: GeneralService,
    private spinner: NgxSpinnerService
  ) {
    this.config = this.configService.templateConf;
    this.EduCompId = this.authService.getEduCompId();
    if (this.EduCompId) this.onChangeEduCompId(this.EduCompId);
    this.branchId = this.authService.getBranchId();
  }

  ngOnInit() {
    if (this.authService.isUser()) {
      this.userFunctionsService.getUserFunctions(this.branchId);
      this.userFunctionsService.menuItems.subscribe((menu) => {
        this.menuItems = menu;
        // console.log("horizontal-menu:", menu);
      });
    }

    if (
      this.authService.isUser() &&
      (!this.authService.isStudent() || !this.authService.isSupport())
    ) {
      this.getEduCompByUser();
    }

    if (!this.authService.isUser()) {
      this.menuItems = [];
    }
    // this.menuItems = routs;
  }

  ngAfterViewInit() {
    this.authService.branchId.subscribe((e) => {
      this.branchId = e.branchId;
      if (
        this.authService.isUser() &&
        (!this.authService.isStudent() || !this.authService.isSupport())
      ) {
        this.userFunctionsService.getUserFunctions(e.branchId);
      }
    });

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (
        this.authService.isUser() &&
        (!this.authService.isStudent() || !this.authService.isSupport())
      ) {
        this.userFunctionsService.getUserFunctions(e.branchId);
        //  this.onChangeEduCompId(e.EduCompId);
      }
    });

    this.layoutSub = this.configService.templateConf$.subscribe(
      (templateConf) => {
        if (templateConf) {
          this.config = templateConf;
        }
        this.loadLayout();
        this.cdr.markForCheck();
      }
    );
  }

  getEduCompByUser() {
    this.Branches = [];
    this.branchId = "";
    this.GeneralServ.getEduCompByUser<IEducationalCompsVM>().subscribe(
      (res: any) => {
        this.EducationalComps = res;
      }
    );
  }

  onChangeEduCompId(EduCompId: any) {
    this.authService.setEduCompId(EduCompId);
    if (EduCompId !== "null" && EduCompId != "undefined") {
      this.GeneralServ.getBranchesByEduComp(EduCompId).subscribe((res: any) => {
        this.Branches = res;
        this.authService.setBranchId(res[0]?.Id);
        this.branchId = res[0]?.Id;
      });
    }
  }

  changebranchId() {
    this.authService.setBranchId(this.branchId);
  }

  // Return  balanceCheck Data

  loadLayout() {
    if (
      this.config.layout.menuPosition &&
      this.config.layout.menuPosition.toString().trim() != ""
    ) {
      this.menuPosition = this.config.layout.menuPosition;
    }

    if (this.config.layout.variant === "Transparent") {
      this.transparentBGClass = this.config.layout.sidebar.backgroundColor;
    } else {
      this.transparentBGClass = "";
    }
  }

  ngOnDestroy() {
    if (this.layoutSub) {
      this.layoutSub.unsubscribe();
    }
  }
}
