import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import {
  ICourseLectureAddEditVM,
  ILecturesForCourseVM,
  ICoursesForLectureVM,
} from "app/dashboard/models/setup/educational-setup/course";

@Injectable({
  providedIn: "root",
})
export class CourseLectureService {
  constructor(private http: HttpClient) {}

  // get All Courses by Lecture id
  getAllCoursesByLectureId(lectureId: number) {
    return this.http.get<ICoursesForLectureVM[]>(
      `${environment.baseUrl}CourseLecture/getAllSubjectByLectureId/${lectureId}`
    );
  }

  // get All Lectures by Course id
  getAllLecturesIdsByCourseId(courseId: number) {
    return this.http.get<ILecturesForCourseVM[]>(
      `${environment.baseUrl}CourseLecture/getAllLectureByCourseId/${courseId}`
    );
  }

  // get All Courses by Lecture id and year Id
  getAllCoursesByLectureAndYearId(lectureId: number,eduYear:number) {
    return this.http.get<ICoursesForLectureVM[]>(
      `${environment.baseUrl}CourseLecture/getAllSubjectByLectureId_eduYearId/${lectureId}/${eduYear}`
    );
  }

  // add Course Lectures
  addCourseLectures(data: ICourseLectureAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}CourseLecture/addCourseLecture`,
      data
    );
  }

  // edit Course Lectures
  editCourseLectures(data: ICourseLectureAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}CourseLecture/EditCourseLecture`,
      data
    );
  }

  // // Delete Course Lecture
  // deleteCourseLecture(id: number) {
  //   return this.http.delete(
  //     `${environment.baseUrl}CourseLecture/deleteCourseLecture/${id}`
  //   );
  // }
}
