import { RouteInfo } from "./../../vertical-menu/vertical-menu.metadata";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { BehaviorSubject } from "rxjs";
import { map } from "rxjs/operators";
@Injectable({
  providedIn: "root",
})
export class UserFunctionsService {
  menuItems = new BehaviorSubject<any>([]);

  constructor(private http: HttpClient) {}

  getUserFunctions(branchId: any) {
    return this.http
      .get(`${environment.baseUrl}usermanagement/return-functions/${branchId}`)
      .subscribe((res: any) => {
        // console.log("UserFunctions Service:", res);

        // Functions has clssification and group
        res.function_clssification?.map((clssification) => {
          clssification.class = "dropdown nav-item has-sub";
          clssification.icon = "ft-align-left";
          clssification.isExternalLink = false;
          clssification.badge = "";
          clssification.badgeClass =
            "badge badge-pill badge-danger float-right mr-1 mt-1";
          clssification.Groups?.map((submenu) => {
            submenu.class = "dropdown nav-item has-sub";
            submenu.icon = "ft-align-left";
            submenu.isExternalLink = false;
            submenu.badge = "";
            submenu.badgeClass = "";
            submenu.function?.map((subItem) => {
              subItem.title = subItem.title;
              subItem.path = subItem.path;
              subItem.icon = "ft-arrow-right submenu-icon";
              subItem.isExternalLink = false;
              subItem.badge = "";
              subItem.badgeClass = "";
            });
            submenu.submenu = submenu.function;
          });
          clssification.submenu = clssification.Groups;
        });

        // Functions has clssification only and is one Classifcation
        if (res.functionGroupless?.length == 1) {
          res.functionGroupless?.map((groupless) => {
            groupless.function?.map((subItem) => {
              subItem.title = subItem.title;
              subItem.path = subItem.path;
              subItem.icon = "ft-align-left submenu-icon";
              subItem.isExternalLink = false;
              subItem.badge = "";
              subItem.badgeClass = "";
              subItem.submenu = [];
            });
            res.functionGroupless = groupless.function;
          });
        } else {
          res.functionGroupless?.map((groupless) => {
            groupless.class = "dropdown nav-item has-sub";
            groupless.icon = "ft-align-left";
            groupless.isExternalLink = false;
            groupless.badge = "";
            groupless.badgeClass =
              "badge badge-pill badge-danger float-right mr-1 mt-1";
            groupless.function?.map((subItem) => {
              subItem.title = subItem.title;
              subItem.path = subItem.path;
              subItem.icon = "ft-arrow-right submenu-icon";
              subItem.isExternalLink = false;
              subItem.badge = "";
              subItem.badgeClass = "";
              subItem.submenu = [];
            });
            groupless.submenu = groupless.function;
          });
        }

        let functions = res.function_clssification.concat(
          res.functionGroupless
        );

        this.menuItems.next(functions);
        // this.menuItems.subscribe((fun) => {
        //   console.log("fun", fun);
        // });
      });
  }
}
