import { TranslateService } from "@ngx-translate/core";
import { Component, OnInit } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { ILeaderBoardVM } from "app/shared/models/general/general";
import { UploadFileService } from "app/shared/services/General/upload-file.service";

@Component({
  selector: "app-leader-board",
  templateUrl: "./leader-board.component.html",
  styleUrls: ["./leader-board.component.scss"],
})
export class LeaderBoardComponent implements OnInit {
  LeaderBoardList: ILeaderBoardVM[] = [
    {
      name: "salma mohamed",
      image_path: "assets/img/portrait/small/avatar-s-10.png",
    },
    {
      name: "ahmed mohamed",
      image_path: "assets/img/portrait/small/avatar-s-9.png",
    },
    {
      name: "mohamed",
      image_path: "assets/img/portrait/small/avatar-s-1.png",
    },
  ];

  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private GeneralServ: GeneralService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private uploadFileService: UploadFileService,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
 //   this.getLeaderBoardData();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
   //   this.getLeaderBoardData();
    });
  }

  getLeaderBoardData() {
    this.spinner.show();
    this.GeneralServ.getLeaderBoardData().subscribe((res: any) => {
      if (res) this.LeaderBoardList = res;
      this.spinner.hide();
    });
  }
}
