import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { GovernorateGetVM } from "app/dashboard/models/setup/general-setup/location/governorate";
import { GovernorateService } from "app/dashboard/services/setup/general-setup/location/governorate.service";

@Component({
  selector: "app-governorate-dropdown",
  templateUrl: "./governorate-dropdown.component.html",
  styleUrls: ["./governorate-dropdown.component.scss"],
})
export class GovernorateDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  governerateList: GovernorateGetVM[];
  constructor(
    private governorateService: GovernorateService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.governorateService.getAllGovernoratesDropDown().subscribe((res) => {
      if (res) this.governerateList = res;
    });
    this.control.valueChanges.subscribe((val) => {
      console.log(val);
    });
  }
}
