import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { FormControl, FormControlName, FormGroup } from "@angular/forms";
import { MatSelectChange } from "@angular/material/select";
import { TranslateService } from "@ngx-translate/core";
import { IStageVM } from "app/dashboard/models/setup/general-setup/stage";
import { StagesService } from "app/dashboard/services/setup/general-setup/stages.service";

@Component({
  selector: "app-educational-stage-dropdown",
  templateUrl: "./educational-stage-dropdown.component.html",
  styleUrls: ["./educational-stage-dropdown.component.scss"],
})
export class EducationalStageDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  stagesList: IStageVM[];

  constructor(
    private stagesService: StagesService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.stagesService.getAllStagesDropdown().subscribe((res: IStageVM[]) => {
      if (res) {
        this.stagesList = res;
        console.log("getAllStagesDropdown", res);
      }
    });

    this.control.valueChanges.subscribe((control) => {
      console.log("StageDropdown-StageId: ", control);
    });
  }
}
