import { Component, Input, OnInit } from "@angular/core";
import { IBranchesVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";

@Component({
  selector: "app-branchs-by-educompid-dropdown",
  templateUrl: "./branchs-by-educompid-dropdown.component.html",
  styleUrls: ["./branchs-by-educompid-dropdown.component.scss"],
})
export class BranchsByEducompidDropdownComponent implements OnInit {
  @Input() EduCompId: number;
  branchId: number = null;
  Branches: IBranchesVM[];

  constructor(
    public AuthService: AuthService,
    private GeneralServ: GeneralService
  ) {
    this.branchId = this.AuthService.getBranchId();
    this.EduCompId = this.AuthService.getEduCompId();
    this.getBranchesByEduComp();
  }
  ngOnInit(): void {}

  ngAfterViewInit() {
    this.AuthService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      console.log("from branch", this.EduCompId);
      this.getBranchesByEduComp();
    });
  }

  getBranchesByEduComp() {
    this.Branches = [];
    this.branchId = null;
    if (this.EduCompId > 0) {
      this.GeneralServ.getBranchesByEduComp(this.EduCompId).subscribe(
        (res: any) => {
          this.Branches = res;
          this.AuthService.setBranchId(res[0]?.Id);
          this.branchId = res[0]?.Id;
        }
      );
    } else this.AuthService.setBranchId(null);
  }

  changebranchId($event) {
    this.AuthService.setBranchId($event.value);
  }
}
