import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { IExpressioModel } from "app/dashboard/models/setup/general-setup/appreciation-expressions";
import { ClassRoomGetVM } from "app/dashboard/models/setup/general-setup/classes/class-room";
import { AppreciationsService } from "app/dashboard/services/setup/general-setup/appreciations.service";
import { StudentService } from "app/dashboard/services/setup/student/student.service";
import { ParentService } from "app/parent/services/parent.service";
import {
  IAppreciationSendVM,
  ISimpleUserVM,
} from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { MessagesService } from "app/shared/services/messages.service";
import { TeacherService } from "app/teacher/services/teacher.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-send-appreciation",
  templateUrl: "./send-appreciation.component.html",
  styleUrls: ["./send-appreciation.component.scss"],
})
export class SendAppreciationComponent implements OnInit {
  // use if teacher
  classroomList: ClassRoomGetVM[] = [];
  // to users (studentList > if teacher) || (teacherList > if parent)
  toUserList: ISimpleUserVM[] = []; //
  // use if parent & teacher
  expressionsList: IExpressioModel[] = [];

  myForm: FormGroup;
  submitted: boolean = false;
  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private teacherService: TeacherService,
    private appreciationExpressionService: AppreciationsService,
    private parentService: ParentService,
    private studentService: StudentService,
    private generalService: GeneralService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      // id: [0],
      classroomId: ["", [Validators.required]],
      ToUsers: [
        {
          value: [],
          disabled: this.authserv.isTeacher() ? true : false,
        },
        [Validators.required],
      ],
      // expression: [
      //   "" /* , [Validators.required, CustomeValidator.whiteSpace] */,
      // ],
      AppreciationExpressionId: ["", [Validators.required]],
    });

    this.getAllAppreciationExpressions();
    if (this.authserv.isTeacher()) this.getAllClassroomsOfTeacher();
    if (this.authserv.isParent()) {
      this.getAllTeachersOfParentChildren();
      this.formCtrls.classroomId.clearValidators();
      this.formCtrls.classroomId.updateValueAndValidity();
    }
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllAppreciationExpressions();
      if (this.authserv.isTeacher()) this.getAllClassroomsOfTeacher();
      if (this.authserv.isParent()) this.getAllTeachersOfParentChildren();
    });
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get Classrooms
  getAllClassroomsOfTeacher() {
    this.spinner.show();
    this.teacherService.getClassesOfTeacher().subscribe((res) => {
      if (res) this.classroomList = res;
      this.spinner.hide();
    });
  }

  // get Teachers
  getAllTeachersOfParentChildren() {
    this.spinner.show();
    this.parentService.getAllChildTeachersDropDown().subscribe((res) => {
      if (res) this.toUserList = res;
      this.spinner.hide();
    });
  }

  // get Appreciation Expression
  getAllAppreciationExpressions() {
    this.spinner.show();
    this.appreciationExpressionService
      .getAllExpressionsDropDown()
      .subscribe((res) => {
        if (res) this.expressionsList = res;
        // console.log("expressions", res);
        this.spinner.hide();
      });
  }

  // get students in classroom
  onChangeClassroom(classroomId: number) {
    // console.log("classid", classroomId);
    this.formCtrls?.ToUsers.setValue(null);

    if (classroomId) {
      this.spinner.show();
      this.studentService
        .GetAllStudentsByClassroomId(classroomId)
        .subscribe((res) => {
          if (res) {
            // console.log("students", res);
            this.toUserList = res;
            this.formCtrls?.ToUsers.enable();
          }
          this.spinner.hide();
        });
    } else this.formCtrls?.ToUsers.disable();
  }

  // send appreciation
  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let appreciation: IAppreciationSendVM = {
        // eduCompId: this.EduCompId,
        // FreeText: this.formCtrls.expression?.value,
        ToUsers: this.formCtrls.ToUsers?.value,
        AppreciationExpressionId:
          this.formCtrls.AppreciationExpressionId?.value,
      };
      console.log("appreciation", appreciation);

      this.spinner.show();
      this.generalService
        .sendAppreciation(appreciation)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.ToastrMsg.success("General.SendSuccessfully");
            this.myForm.reset();
            this.submitted = false;
          } else this.ToastrMsg.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.ToastrMsg.error("General.FormNotValid");
  }
}
