import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { IAcademicYear } from "app/dashboard/models/setup/educational-setup/academic-year";
import { AcademicYearService } from "app/dashboard/services/setup/educational-setup/academic-year.service";

@Component({
  selector: "app-academic-year-dropdown",
  templateUrl: "./academic-year-dropdown.component.html",
  styleUrls: ["./academic-year-dropdown.component.scss"],
})
export class AcademicYearDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  AcademicYears: IAcademicYear[];

  constructor(
    private AcademicYearservice: AcademicYearService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.AcademicYearservice.getYearsDropdown().subscribe((res) => {
      if (res) this.AcademicYears = res.academicEduYearList;
    });
  }
}
