import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { IExpressioModel, IExpressionList } from "app/dashboard/models/setup/general-setup/appreciation-expressions";

@Injectable({
  providedIn: "root",
})
export class AppreciationsService {
  constructor(private http: HttpClient) {}

  gatExpressions(page: number) {
    return this.http.get<IExpressionList>(
      `${environment.baseUrl}AppreciationExpressions/return-all/${page}`
    );
  }

  getAllExpressionsDropDown() {
    return this.http.get<IExpressioModel[]>(
      `${environment.baseUrl}AppreciationExpressions/returnUserAppreciationExpression`
    );
  }

  gatExpressionById(Id: number) {
    return this.http.get(
      `${environment.baseUrl}AppreciationExpressions/GetAppreciationExpressionsById/${Id}`
    );
  }

  addUpdateExpression(body: any) {
    return this.http.post(
      `${environment.baseUrl}AppreciationExpressions/CreateAppreciationExpression`,
      body
    );
  }

  updateExpression(id: number, body: any) {
    return this.http.post(
      `${environment.baseUrl}AppreciationExpressions/UpdateAppreciationExpressions/${id}`,
      body
    );
  }

  deleteExpression(id: number) {
    return this.http.delete(
      `${environment.baseUrl}AppreciationExpressions/DeleteAppreciationExpressionsById/${id}`

    );
  }
}
