import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { ClassRoomGetVM } from "app/dashboard/models/setup/general-setup/classes/class-room";

@Injectable({
  providedIn: "root",
})
export class TeacherService {
  constructor(private http: HttpClient) {}

  // get all classrooms with current teacher
  getClassesOfTeacher() {
    return this.http.get<ClassRoomGetVM[]>(
      `${environment.baseUrl}Teacher/getAllTeacherClassrooms`
    );
  }
}
