import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ILessonVM } from "app/dashboard/models/setup/educational-setup/cource-structure";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";

@Component({
  selector: "lessons-by-unit-dropdown",
  templateUrl: "./lessons-dropdown.component.html",
  styleUrls: ["./lessons-dropdown.component.scss"],
})
export class LessonsByUnitDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() unitControl: FormControl;
  @Input() submitted: boolean;
  lessons: ILessonVM[];
  // lessons = [
  //   { ar_name: "1", id: "1" },
  //   { ar_name: "2", id: "2" },
  //   { ar_name: "3", id: "3" },
  // ];
  constructor(private courceStructureService: CourceStructureService) {}

  ngOnInit(): void {
    this.control.disable();

    this.unitControl.valueChanges.subscribe((unitId) => {
      if (unitId > 0) {
        this.getLessons(unitId);
      } else this.control.setValue(null);
      console.log("lesson-Dropdown-unitId", unitId);
    });
  }

  getLessons(unitId: number) {
    this.courceStructureService
      .getLessons(unitId)
      .subscribe((res: ILessonVM[]) => {
        if (res) this.lessons = res;
      });
  }
}
