import { AuthService } from "app/shared/services/auth/auth.service";
import { ActivatedRoute, Router } from "@angular/router";
import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-parent-children-dropdown",
  templateUrl: "./parent-children-dropdown.component.html",
  styleUrls: ["./parent-children-dropdown.component.scss"],
})
export class ParentChildrenDropdownComponent implements OnInit {
  userId: number;
  children = [
    { name: "Sayed", userId: "6028" },
    { name: "child 2", userId: "2" },
  ];
  constructor(private router: Router, private authService: AuthService) {
    this.userId = this.authService.getCurrentParentChildIdd();
    console.log("ParentChildrenDropdown", this.userId);
  }

  ngOnInit(): void {}

  changeChild($event) {
    if ($event.value) {
      this.authService.setCurrentParentChildIdd($event.value);
      this.router.navigateByUrl("/parent/child-evaluation/" + $event.value);
    }
  }

  onClick(userId: number) {
    this.router.navigateByUrl("/parent/child-evaluation/" + userId);
    this.authService.setCurrentParentChildIdd(userId);
  }
}
