import { Component, OnInit } from "@angular/core";
import { IEducationalCompsVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";

@Component({
  selector: "app-educomp-list-dropdown",
  templateUrl: "./educomp-list-dropdown.component.html",
  styleUrls: ["./educomp-list-dropdown.component.scss"],
})
export class EducompListDropdownComponent implements OnInit {
  EducationalComps: IEducationalCompsVM[];
  EduCompId: number;

  constructor(
    public AuthService: AuthService,
    private GeneralServ: GeneralService
  ) {
    this.EduCompId = this.AuthService.getEduCompId();
  }

  ngOnInit(): void {
    if (
      !(
        (this.AuthService.isStudent() || this.AuthService.isSupport())

        //  || this.AuthService.isParent()
      )
    ) {
      this.GeneralServ.getEduCompByUser<IEducationalCompsVM>().subscribe(
        (res: any) => {
          this.EducationalComps = res;
        }
      );
    }
  }

  onChangeEduCompId($event) {
    console.log($event.value);
    this.AuthService.setEduCompId($event.value);
  }
}
