import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";

@Component({
  selector: "all-cources-dropdown",
  templateUrl: "./all-cources-dropdown.component.html",
  styleUrls: ["./all-cources-dropdown.component.scss"],
})
export class AllCourcesDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  Courses: CourseGetVM[];

  constructor(
    private courseService: CourseService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.courseService.getAllCoursesDropDown().subscribe((res) => {
      if (res) this.Courses = res;
    });
  }
}
