import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import {
  CityAddEditVM,
  CityGetVM,
  CityGetResponseVM,
} from "app/dashboard/models/setup/general-setup/location/city";

@Injectable({
  providedIn: "root",
})
export class CityService {
  constructor(private http: HttpClient) {}

  // Return All  Cities by page
  getAllCities(page: number) {
    return this.http.get<CityGetResponseVM>(
      `${environment.baseUrl}City/getCityByPage/${page}`
    );
  }

  // Return All Cities by governorate id > dropDown list
  getAllCitiesByGovernorateId(id: number) {
    return this.http.get<CityGetVM[]>(
      `${environment.baseUrl}City/getAllCitiesByGovId/${id}`
    );
  }

  // // Return All Cities > dropDown list
  // getAllCitiesDropDown() {
  //   return this.http.get<CityGetVM[]>(`${environment.baseUrl}City/getAllCities`);
  // }

  // Add - edit New City
  addEditCity(data: CityAddEditVM) {
    return this.http.post(`${environment.baseUrl}City/addEditCity`, data);
  }

  // Delete City
  deleteCity(id: number) {
    return this.http.delete(`${environment.baseUrl}City/deleteCity/${id}`);
  }
}
