import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import {
  IAcademicYear,
  IAcademicYearList,
  IAddEditAcademicYear,
} from "app/dashboard/models/setup/educational-setup/academic-year";

@Injectable({
  providedIn: "root",
})
export class AcademicYearService {
  constructor(private http: HttpClient) {}

  // Return All Academic Years
  getAcademicYears(page: number) {
    return this.http.get<IAcademicYearList>(
      `${environment.baseUrl}AcademicEduYear/getAcademicEducationalYear/${page}`
    );
  }

  getYearsDropdown() {
    return this.http.get<{ academicEduYearList: IAcademicYear[] }>(
      `${environment.baseUrl}AcademicEduYear/getAllAcademicEducationalYear`
    );
  }

  // getYearsByYearId(yearId: number) {
  //   return this.http.get<{ academicEduYearList: IAcademicYear[] }>(
  //     `${environment.baseUrl}AcademicEduYear/getAcademicEducationalYearByYearId/${yearId}`
  //   );
  // }

  // getYearsByYEduCompId(eduCompId: number) {
  //   return this.http.get<{ academicEduYearList: IAcademicYear[] }>(
  //     `${environment.baseUrl}AcademicEduYear/getAcademicEducationalYearByEduCompId/${eduCompId}`
  //   );
  // }

  //Add - edit Academic Year
  addEditAcademicYear(data: IAddEditAcademicYear) {
    return this.http.post(
      `${environment.baseUrl}AcademicEduYear/addEditAcademicEducationalYear`,
      data
    );
  }

  //Delete Academic  Year
  deleteAcademicYear(AcademicYearId: number) {
    return this.http.delete(
      `${environment.baseUrl}AcademicEduYear/DeleteAcademicEducationalYear/${AcademicYearId}`
    );
  }
}
