import { UserFunctionsService } from "./user-functions.service";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { environment } from "environments/environment";
import { Subject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  EduCompId = new Subject<any>();
  branchId = new Subject<any>();
  currentParentChildId = new Subject<any>();

  constructor(
    private http: HttpClient,
    private router: Router,
    private UserFunctionsService: UserFunctionsService
  ) {}

  // START Registeration
  Registeration<T, TE>(data: T) {
    return this.http.post<TE>(environment.baseUrl + "APIUsers/Register", data);
  }

  // START LOGIN
  login<T, TE>(loginData: T) {
    return this.http.post<TE>(environment.baseUrl + "user/login", loginData);
  }

  //START LOGOUT
  logout() {
    Object.keys(localStorage).forEach((key) => {
      if (!key.includes("currentLang")) delete localStorage[key];
    });
    this.UserFunctionsService.menuItems.next(null);
    this.router.navigateByUrl("");
  }

  // START Forgot Password
  forgotPassword(email: any) {
    return this.http.post(
      environment.baseUrl + "APIUsers/forgotPassword",
      email
    );
  }

  // START Reset Password
  resetPassword(data: any) {
    return this.http.post(
      environment.baseUrl + "APIUsers/resetPasswordByVerificationCode",
      data
    );
  }

  getUserRowFunctions(parentId: any) {
    return this.http.get(
      `${environment.baseUrl}usermanagement/return-children-functions-by-parentId/${parentId}`
    );
  }

  //================= Start  Chick Users ==================
  isUser() {
    return !!localStorage.getItem("token");
  }

  isTeacher() {
    if (localStorage.getItem("user_type_Id") == "1" && this.isUser()) {
      return true;
    } else return false;
  }

  isStudent() {
    if (localStorage.getItem("user_type_Id") == "2" && this.isUser()) {
      return true;
    } else return false;
  }

  isParent() {
    if (localStorage.getItem("user_type_Id") == "3" && this.isUser()) {
      return true;
    } else return false;
  }

  isAdmin() {
    if (localStorage.getItem("user_type_Id") == "4" && this.isUser()) {
      return true;
    } else return false;
  }

  isCenter() {
    if (localStorage.getItem("user_type_Id") == "5" && this.isUser()) {
      return true;
    } else return false;
  }

  isSupport() {
    if (localStorage.getItem("user_type_Id") == "6" && this.isUser()) {
      return true;
    } else return false;
  }

  isFinancial() {
    if (localStorage.getItem("user_type_Id") == "7" && this.isUser()) {
      return true;
    } else return false;
  }

  isAssistant() {
    if (localStorage.getItem("user_type_Id") == "8" && this.isUser()) {
      return true;
    } else return false;
  }

  getToken() {
    return localStorage.getItem("token") || null;
  }

  getUserId(): number {
    return Number(localStorage.getItem("userId"));
  }

  getClassId(): number {
    //student ClassId
    return Number(localStorage.getItem("classId"));
  }

  getYearId(): number {
    //student YearId
    return Number(localStorage.getItem("yearId"));
  }

  setBranchId(branchId) {
    localStorage.setItem("branchId", branchId || null);
    this.branchId.next({ branchId });
  }

  getEduCompId(): number {
    return Number(localStorage.getItem("EduCompId"));
  }

  setEduCompId(EduCompId) {
    localStorage.setItem("EduCompId", EduCompId || null);
    this.EduCompId.next({ EduCompId });
  }

  getBranchId(): number {
    return Number(localStorage.getItem("branchId"));
  }

  setCurrentParentChildIdd(userId) {
    localStorage.setItem("currentParentChildId", userId || null);
    this.currentParentChildId.next(userId);
  }

  getCurrentParentChildIdd() {
    return Number(localStorage.getItem("currentParentChildId"));
  }
} //end class
