import { map, shareReplay } from "rxjs/operators";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import {
  IAppreciationBoardVM,
  IAppreciationSendVM,
  ILeaderBoardVM,
  IUserTypeVM,
} from "app/shared/models/general/general";

@Injectable({
  providedIn: "root",
})
export class GeneralService {
  constructor(private http: HttpClient) {}
  getAllEduCompList() {
    return this.http.get(environment.baseUrl + "EduComponent");
  }

  returnProfile() {
    return this.http.get(environment.baseUrl + "AJAX/returnProfile");
  }

  getUser(userId: number) {
    return this.http.get(`${environment.baseUrl}AJAX/getUser/${userId}`).pipe(
      map((res) => res),
      shareReplay()
    );
  }

  editProfile(data: any) {
    return this.http.post(environment.baseUrl + "AJAX/editProfile", data);
  }
  getEduCompByUser<T>() {
    return this.http.get<T>(
      environment.baseUrl + "AJAX/returnEducationalComponentByUser"
    );
  }

  getBranchesByEduComp(EduCompId: number) {
    return this.http.get(
      `${environment.baseUrl}usermanagement/returBranchesByEduCompId/${EduCompId}`
    );
  }

  getTeachersByEducompId(EduCompId: any) {
    return this.http.get(
      environment.baseUrl + "Admin/get-all-teachers/" + EduCompId
    );
  }

  getTeacherBySubjectAndEduCompId(EduCompId: any, subjectId: any) {
    return this.http.get(
      `${environment.baseUrl}Admin/getTeachersByEduCompID/${EduCompId}/${subjectId}`
    );
  }

  getAllNotifications(yearId: any, page: any) {
    return this.http.get(
      environment.baseUrl +
        "AJAX/returnNotificationsByUserId/" +
        yearId +
        "/" +
        page
    );
  }

  ReadNotification(id: any) {
    return this.http.get(environment.baseUrl + "AJAX/ReadNotification/" + id);
  }

  getLeaderBoardData() {
    return this.http.get<ILeaderBoardVM>(
      `${environment.baseUrl}general/getLeaderBoardData`
    );
  }

  getAppreciationBoardData(userId: number) {
    return this.http.get<IAppreciationBoardVM>(
      `${environment.baseUrl}AppreciationExpressions/GetMyAppreciationExpressions/${userId}`
    );
  }

  sendAppreciation(data: IAppreciationSendVM) {
    return this.http.post(
      `${environment.baseUrl}AppreciationExpressions/CreateTeachersStudentsAppreciations`,
      data
    );
  }

  getAllUserTypes() {
    return this.http.get<{ UserTypeList: IUserTypeVM[] }>(
      `${environment.baseUrl}UserType/GetAllUserTypes`
    );
  }
}
