import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  AreaAddEditVM,
  AreaGetResponseVM,
  AreaGetVM,
} from "app/dashboard/models/setup/general-setup/location/area";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class AreaService {
  constructor(private http: HttpClient) {}

  // Return All Areas by page
  getAllAreas(page: number) {
    return this.http.get<AreaGetResponseVM>(
      `${environment.baseUrl}Area/getAreaByPage/${page}`
    );
  }

  // Return All Areas by city id > dropDown list
  getAllAreasByCityId(id: number) {
    return this.http.get<AreaGetVM[]>(
      `${environment.baseUrl}Area/getAllAreasByCityId/${id}`
    );
  }

  // // Return All Areas > dropDown list
  // getAllAreasDropDown() {
  //   return this.http.get<AreaGetVM[]>(`${environment.baseUrl}Area/getAllAreas`);
  // }

  // Add - edit Area
  addEditArea(data: AreaAddEditVM) {
    return this.http.post(`${environment.baseUrl}Area/addEditArea`, data);
  }

  // Delete Area
  deleteArea(id: number) {
    return this.http.delete(`${environment.baseUrl}Area/deleteArea/${id}`);
  }
}
