import { TranslateService } from "@ngx-translate/core";
import { Component, OnInit } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute, Router } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import {
  IAppreciationBoardVM,
  IUserVM,
  UserModel,
} from "app/shared/models/general/general";
import { UploadFileService } from "app/shared/services/General/upload-file.service";

@Component({
  selector: "app-appreciation-board",
  templateUrl: "./appreciation-board.component.html",
  styleUrls: ["./appreciation-board.component.scss"],
})
export class AppreciationBoardComponent implements OnInit {
  AppreciationBoardList: IAppreciationBoardVM[] = [];

  currentLang: string;
  EduCompId: number;
  functionId: number;
  userId: number;
  childName: string;

  constructor(
    private generalService: GeneralService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private uploadFileService: UploadFileService,
    public authService: AuthService
  ) {
    //use after auth for function id and row functions
    route.paramMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = authService.getEduCompId();
  }

  ngOnInit(): void {
    //UserId If chid
    this.userId = this.authService.getCurrentParentChildIdd();
    if (this.userId > 0) {
      this.getAppreciationBoardData();
      this.generalService
        .getUser(this.authService.getCurrentParentChildIdd())
        .subscribe((res: IUserVM) => {
          this.childName = res.user.ar_name;
        });
    }

    // if child changed
    this.authService.currentParentChildId.subscribe((userId: number) => {
      console.log(userId);
      if (userId > 0) {
        this.getAppreciationBoardData();
        this.generalService.getUser(userId).subscribe((res: IUserVM) => {
          this.childName = res.user.ar_name;
        });
      }
    });

    // userId if studnt or teacher
    if (this.userId <= 0) {
      this.userId = this.authService.getUserId();
      if (this.userId > 0) this.getAppreciationBoardData();
    }
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAppreciationBoardData();
    });
  }

  getAppreciationBoardData() {
    this.spinner.show();
    this.generalService
      .getAppreciationBoardData(this.userId)
      .subscribe((res: any) => {
        if (res) this.AppreciationBoardList = res;
        this.spinner.hide();
      });
  }

  ngOnDestroy(): void {
    if (this.authService.getCurrentParentChildIdd())
      this.authService.setCurrentParentChildIdd(null);
  }
}
