import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IPaymentMethodsVM } from "app/dashboard/models/setup/financial-setup/payment-methods";
import { PaymentMethodService } from "app/dashboard/services/setup/financial-setup/payment-method.service";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-payment-methods-dropdown",
  templateUrl: "./payment-methods-dropdown.component.html",
  styleUrls: ["./payment-methods-dropdown.component.scss"],
})
export class PaymentMethodsDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  EduCompId: number;

  // PaymentMethods: IPaymentMethodsVM[];
  PaymentMethods = [
    { ar_name: "1", id: "1" },
    { ar_name: "2", id: "2" },
    { ar_name: "3", id: "3" },
  ];

  constructor(
    private PaymentMethodService: PaymentMethodService,
    public authService: AuthService
  ) {
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.PaymentMethodService.getAllPaymentMethodsDropdown(
      this.EduCompId
    ).subscribe((res: IPaymentMethodsVM[]) => {
      // if (res) this.PaymentMethods = res;
    });
  }
}
