  //#region ///////////////////////////////// work days
  getAllWorkDays() {
    return this.http.get<IWorkDayVM[]>(
      `${environment.baseUrl}WorkDays/getAllWorkDays`
    );
  }

  getWorkDayById(id: number) {
    console.log("iddd", id);
    return this.http.get<IWorkDayDetailsVM[]>(
      `${environment.baseUrl}WorkDays/getWorkDaysByWorkDayId/${id}`
    );
  }

  getAllWorkDaysByPage(page: number) {
    return this.http.get<IWorkDayListVM>(
      `${environment.baseUrl}WorkDays/getWorkDaysByPage/${page}`
    );
  }

  addEditWorkDay(workDay: IWorkDayAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}WorkDays/addEditWorkDay`,
      workDay
    );
  }

  deleteWorkDay(id: number) {
    return this.http.delete(
      `${environment.baseUrl}WorkDays/deleteWorkDays/${id}`
    );
  }
  //#endregion

  //#region ///////////////////////////////// work days details
  getAllWorkDayDetails() {
    return this.http.get<IWorkDayDetailsVM[]>(
      `${environment.baseUrl}WorkDayDetails/getAllWorkDayDetails`
    );
  }

  // getWorkDayDetailsById(id: number) {
  //   return this.http.get<IWorkDayDetailsVM>(
  //     `${environment.baseUrl}WorkDayDetails/getAllWorkDayDetailsById/${id}`
