  getAllTeacherBySubjectId(subjectId) {
    return this.http.get(
      `${environment.baseUrl}Teacher/getAllTeacherSubjectBySubjectIdByPage/${subjectId}`
    );
  }

  addEditEducationalTable(data: IEducationalAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}EduTablesDetails/addEditEduTablesDetails`,
      data
    );
  }

