import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { ITermGetVM } from "app/dashboard/models/setup/educational-setup/terms";
import { TermsService } from "app/dashboard/services/setup/educational-setup/terms.service";

@Component({
  selector: "app-term-by-academic-year",
  templateUrl: "./term-by-academic-year.component.html",
  styleUrls: ["./term-by-academic-year.component.scss"],
})
export class TermByAcademicYearComponent implements OnInit {
  TermList: ITermGetVM[];
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() academicEduYearlId: FormControl;
  curLang: string;

  constructor(
    private TermsServ: TermsService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }
  ngOnInit(): void {

  this.control.disable();

  this.academicEduYearlId.valueChanges.subscribe((Id) => {
    this.control.setValue(null);

    if (Id > 0) {
      this.control.enable();
      this.getAllTeacher(Id);

    } else {
      this.control.disable();
    }
  });
}

// get all terms
getAllTeacher(Id: number) {

  this.TermsServ.getTermsByYearIdDropdown(Id).subscribe(
    (res) => {
      if (res) {
        this.TermList = res;
        console.log("term",this.TermList)
        console.log("edu",this.academicEduYearlId.value)

        this.control.enable();
      }
    }
  );
}}
