import { Injectable } from "@angular/core";
import { IfileVM } from "app/shared/models/general/general";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class UploadFileService {
  constructor() {}

  async uploadFile(event: any) {
    let file = event.target.files[0];
    var reader = new FileReader();
    let data = {} as IfileVM;
    reader?.readAsDataURL(file);
    reader.onload = (event: any) => {
      data.FileAsBase64 = event.target.result;
    };
    data.name = file.name;
    data.size = file.size;
    data.type = file.type;
    data.LastModified = file.lastModified;
    data.LastModifiedDate = file.lastModifiedDate;
    return await data;
  }

  downloadFile(url: string) {
    window.open(environment.baseUrl.replace("/api/", "") + url, "_blank");
  }

  previewPicture(url: any) {
    if (url) {
      if (!url.FileAsBase64)
        return `${environment.baseUrl.replace("/api/", "")}${url}`;
      else return url.FileAsBase64;
    }
  }
}
