import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { ICoursesForLectureVM } from "app/dashboard/models/setup/educational-setup/course";
import { CourseLectureService } from "app/dashboard/services/setup/educational-setup/course-lecture.service";

@Component({
  selector: "app-courses-by-lecture",
  templateUrl: "./courses-by-lecture.component.html",
  styleUrls: ["./courses-by-lecture.component.scss"],
})
export class CoursesByLectureComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() lecId: number;
  @Input() eduYearControl: number;

  curLang: string;
  Courses: ICoursesForLectureVM[];

  constructor(
    private CourseLectureServ: CourseLectureService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    if(this.lecId>0){
      this.CourseLectureServ.getAllCoursesByLectureAndYearId(this.lecId,this.eduYearControl).subscribe(
        (res) => {
          if (res) this.Courses = res;
          console.log(res);
        }
      );
    }

  }
}
