import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ISimpleUserVM } from "app/shared/models/general/general";
import { environment } from "environments/environment";
import { AddChidModel, TeacherRateBodyModel } from "../models/parent";

@Injectable({
  providedIn: "root",
})
export class ParentService {
  constructor(private http: HttpClient) {}

  getAllChildren() {
    return this.http.get(`${environment.baseUrl}parent/get-all-parent-childern`);
  }

  getChildrenDropdown() {
    return this.http.get(
      `${environment.baseUrl}parent/get-all-parent-childern-drop`
    );
  }

  addChild(body: AddChidModel) {
    return this.http.post(
      `${environment.baseUrl}parent/get-all-parent-childern-drop`,
      body
    );
  }

  deleteChild(childId: number) {
    return this.http.delete(
      `${environment.baseUrl}parent/deleteChild/${childId}`
    );
  }

  rateTeacher(body: TeacherRateBodyModel) {
    return this.http.post(`${environment.baseUrl}parent/rate-teacher`, body);
  }

  getTeachersToRate() {
    return this.http.get(
      `${environment.baseUrl}parent/get-childern-teachers-for-rating`
    );
  }

  // get all teachers of parent children
  getAllChildTeachersDropDown() {
    return this.http.get<ISimpleUserVM[]>(
      `${environment.baseUrl}AppreciationExpressions/GetTeachersOfParentStudents`
    );
  }
}
