import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IEducationYear } from "app/dashboard/models/setup/educational-setup/education-year";
import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "app-educational-year-dropdown",
  templateUrl: "./educational-year-dropdown.component.html",
  styleUrls: ["./educational-year-dropdown.component.scss"],
})
export class EducationalYearDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  EducationYears: IEducationYear[];

  constructor(
    private educationYearService: EducationYearService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.getEducationalYears();
  }
  getEducationalYears() {
    this.educationYearService
      .getEducationalYearDropdown()
      .subscribe((res: any) => {
        if (res) {
          this.EducationYears = res.eduYearList;
        }
      });
  }
}
