import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ITopicVM } from "app/dashboard/models/setup/educational-setup/cource-structure";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";

@Component({
  selector: "topics-by-lesson-dropdown",
  templateUrl: "./topics-dropdown.component.html",
  styleUrls: ["./topics-dropdown.component.scss"],
})
export class TopicsByLessonDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() lessonControl: FormControl;
  @Input() submitted: boolean;
  //topics: ITopicVM[];
  topics = [
    { ar_name: "1", id: "1" },
    { ar_name: "2", id: "2" },
    { ar_name: "3", id: "3" },
  ];
  constructor(private courceStructureService: CourceStructureService) {}

  ngOnInit(): void {
    this.control.disable();

    this.lessonControl.valueChanges.subscribe((lessonId) => {
      if (lessonId > 0) {
        this.getTopics(lessonId);
        this.control.enable();
      } else this.control.setValue(null);
      console.log("topic-Dropdown-lessonId", lessonId);
    });
  }

  getTopics(lessonId: number) {
    this.courceStructureService
      .getTopics(lessonId)
      .subscribe((res: ITopicVM[]) => {
        // if (res) this.topics = res;
      });
  }
}
