import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { CityGetVM } from "app/dashboard/models/setup/general-setup/location/city";
import { CityService } from "app/dashboard/services/setup/general-setup/location/city.service";

@Component({
  selector: "app-city-by-governrate-dropdown",
  templateUrl: "./city-by-governrate-dropdown.component.html",
  styleUrls: ["./city-by-governrate-dropdown.component.scss"],
})
export class CityByGovernrateDropdownComponent implements OnInit {
  @Input() cityControl: FormControl;
  @Input() goverControl: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  cityList: CityGetVM[];

  constructor(
    private CityService: CityService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.cityControl.disable();

    this.goverControl?.valueChanges.subscribe((governrateId) => {
      this.cityControl.setValue(null);

      if (governrateId > 0) {
        this.getAllCitys(governrateId);
        this.cityControl.enable();
      } else {
        this.cityControl.disable();
      }
    });
  }

  getAllCitys(governrateId: number) {
    this.CityService.getAllCitiesByGovernorateId(governrateId).subscribe(
      (res) => {
        if (res) this.cityList = res;
      }
    );
  }
}
