import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";

@Component({
  selector: "app-teacher-bysubject-dropdown",
  templateUrl: "./teacher-bysubject-dropdown.component.html",
  styleUrls: ["./teacher-bysubject-dropdown.component.scss"],
})
export class TeacherBysubjectDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() subjectId: FormControl;
  curLang: string;
  teatcherList: any;
  constructor(
    private WeeklyTableServ: WeeklyTableService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.control.disable();

    this.subjectId.valueChanges.subscribe((subId) => {
      this.control.setValue(null);

      if (subId > 0) {
        this.control.enable();

        this.getAllTeacher(subId);
      } else {
        this.control.disable();
      }
    });
  }

  // get all teacher
  getAllTeacher(subId: number) {
    console.log("subId", subId);

    this.WeeklyTableServ.getAllTeacherBySubjectId(subId).subscribe(
      (res: any) => {
        this.teatcherList = res.TeacherSubject_List;
        console.log("teachers", this.teatcherList);
      }
    );
  }
}
