import { ITermAddEdidVM } from "./../../../models/setup/educational-setup/terms";
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import {
  ITermGetVM,
  ITermList,
} from "app/dashboard/models/setup/educational-setup/terms";

@Injectable({
  providedIn: "root",
})
export class TermsService {
  constructor(private http: HttpClient) {}

  // Return All Terms
  getTermsByYearId(yearId: any, page: number) {
    return this.http.get<ITermList>(
      `${environment.baseUrl}Term/getAllTermsByAcademicEduYearIdByPage/${yearId}/${page}`
    );
  }

  getTermsByYearIdDropdown(yearId: number) {
    return this.http.get<ITermGetVM[]>(
      `${environment.baseUrl}Term/getAllTermsByAcademicEduYearId/${yearId}`
    );
  }

  // getAllTerms(page: number) {
  //   return this.http.get<ITermGetVM[]>(
  //     `${environment.baseUrl}Term/getTermByPage/${page}`
  //   );
  // }

  // getTermsDropdown() {
  //   return this.http.get<ITermGetVM[]>(`${environment.baseUrl}Term/getAllTerms`);
  // }

  //Add New Term
  addEditTerm(data: ITermAddEdidVM) {
    return this.http.post(`${environment.baseUrl}Term/addEditTerm`, data);
  }

  //Delete Term
  deleteTerm(TermId: number) {
    return this.http.delete(`${environment.baseUrl}Term/deleteTerm/${TermId}`);
  }
}
