import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class CourceStructureService {
  constructor(private http: HttpClient) {}

  getUnits(subjectId: number) {
    return this.http.get(
      `${environment.baseUrl}Admin/returnSearchedUnitsBySubjectId/${subjectId}`
    );
  }

  getUnitById(id: any) {
    return this.http.get(`${environment.baseUrl}Admin/EditUnitView/${id}`);
  }

  addUpdateUnit(data: any) {
    return this.http.post(
      environment.baseUrl + "Admin/AddSubjectUnitPost",
      data
    );
  }

  deleteUnit(unitId: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/RemoveUnit/${unitId}`,
      null
    );
  }

  getLessons(unitId: any) {
    return this.http.get(
      `${environment.baseUrl}Admin/returnSearchedLessonsByUnitId/${unitId}`
    );
  }

  getLessonById(id: any) {
    return this.http.get(`${environment.baseUrl}Admin/EditLessonView/${id}`);
  }

  addUpdateLesson(unitId: number, body: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/AddSubjectLessonPost/${unitId}`,
      body
    );
  }

  deleteLesson(id: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/RemoveLesson/${id}`,
      null
    );
  }

  getTopicsByLessonId(page: any, LessonId: any) {
    return this.http.get(
      `${environment.baseUrl}Admin/GetTopicsByLessonIdTable/${page}/${LessonId}`
    );
  }

  getTopicById(LessonId: any, id: any) {
    return this.http.get(
      `${environment.baseUrl}Admin/getTopicById/${LessonId}/${id}`
    );
  }
  getTopics(LessonId: any) {
    return this.http.get(`${environment.baseUrl}Admin/getTopicById/${LessonId}`);
  }

  addUpdateTopic(data: any) {
    return this.http.post(environment.baseUrl + "Admin/SubmitTopic", data);
  }

  deleteTopic(id: any) {
    return this.http.post(environment.baseUrl + "Admin/RemoveTopic/" + id, null);
  }
}
