import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { AreaGetVM } from "app/dashboard/models/setup/general-setup/location/area";
import { AreaService } from "app/dashboard/services/setup/general-setup/location/area.service";

@Component({
  selector: "app-area-by-city-dropdown",
  templateUrl: "./area-by-city-dropdown.component.html",
  styleUrls: ["./area-by-city-dropdown.component.scss"],
})
export class AreaByCityDropdownComponent implements OnInit {
  @Input() areaControl: FormControl;
  @Input() cityControl: FormControl;
  @Input() submitted: boolean;
  curLang: string;
  areaList: AreaGetVM[];

  constructor(
    private translate: TranslateService,
    private areaService: AreaService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.areaControl.disable();

    this.cityControl.valueChanges.subscribe((cityId) => {
      this.areaControl.setValue(null);

      if (cityId > 0) {
        this.getAllAreaByCityId(cityId);
        this.areaControl.enable();
      } else {
        this.areaControl.disable();
      }
    });
  }

  getAllAreaByCityId(cityId: number) {
    this.areaService.getAllAreasByCityId(cityId).subscribe((res) => {
      if (res) this.areaList = res;
    });
  }
}
