import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IStudentClassroomAddEditVM,
  IStudentClassroomGetVM,
  IStudentGetResponseVM,
  IStudentGetVM,
} from "app/dashboard/models/setup/student/student";
import { environment } from "environments/environment";
import {
  IStudentInfoGetVM,
  IStudentInfoAddEditVM,
} from "app/dashboard/models/setup/student/application";
import {
  IStudentClassGetResponseVM,
  IStudentClassGetVM,
} from "app/dashboard/models/setup/general-setup/classes/class-room";
import { ISimpleUserVM } from "app/shared/models/general/general";

@Injectable({
  providedIn: "root",
})
export class StudentService {
  constructor(private http: HttpClient) {}

  //#region students
  // Create Student Row and Get Student ID
  addNewStudent() {
    return this.http.get(`${environment.baseUrl}Applications/addNewStudent`);
  }

  // Return All Students
  GetAllStudents(eduCompId: number, page: number) {
    return this.http.get<IStudentGetResponseVM>(
      `${environment.baseUrl}Student/GetAllAcceptedStudentByEduCompIdByPage/${eduCompId}/${page}`
    );
  }

  // Return All Students
  GetAllStudentsDropDown(eduCompId: number) {
    return this.http.get<{ AcceptedStudentList: IStudentGetVM[] }>(
      `${environment.baseUrl}Student/GetAllAcceptedStudentByEduCompId/${eduCompId}`
    );
  }

  // Return All Students
  GetAllStudentsByClassroomId(classroomID: number) {
    return this.http.get<ISimpleUserVM[]>(
      `${environment.baseUrl}ClassRoom/getAllStudentClassRoom/${classroomID}`
    );
  }

  // // Return Student ById
  // getStudentById(id: any) {
  //   return this.http.get<IStudentGetVM>(
  //     `${environment.baseUrl}Applications/getStudentById/${id}`
  //   );
  // }

  //Delete Student
  deleteStudent(id: any) {
    return this.http.delete(
      `${environment.baseUrl}Applications/deleteStudent/${id}`
    );
  }
  //#endregion

  //#region student Info
  // add - edit student info
  addEditApplicationStudentInfo(application: IStudentInfoAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}Student/AddEditStudentApp`,
      application
    );
  }

  // Return student info by id
  getApplicantStudentInfoById(id: number) {
    return this.http.get<IStudentInfoGetVM>(
      `${environment.baseUrl}Student/GetAllStudentInfoByAppId/${id}`
    );
  }
  //#endregion

  //#region add stuent classroom
  getStudentClassroomInfoByStudentId(studentUserId: number) {
    return this.http.get<{ StudentClassItem: IStudentClassroomGetVM }>(
      `${environment.baseUrl}Student/GetStudentClassByStuentUserId/${studentUserId}`
    );
  }

  addEditStudentClassroom(data: IStudentClassroomAddEditVM) {
    return this.http.put(`${environment.baseUrl}Student/AddStudentClass`, data);
  }
  //#endregion

  // use in add students to classroom > row function (not used)
  GetAllStudentsByEduYear(eduYearId: number) {
    return this.http.get<IStudentClassGetVM[]>(
      `${environment.baseUrl}Applications/GetAllStudents/${eduYearId}`
    );
  }
  GetAllStudentsByClassroomIdByPage(classroomID: number, page: number) {
    return this.http.get<IStudentClassGetResponseVM>(
      `${environment.baseUrl}Applications/GetAllStudents/${page}/${classroomID}`
    );
  }
}
