import { Observable } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  ClassRoomAddEditVM,
  ClassRoomDropDownVM,
  ClassRoomGetResponseVM,
  ClassRoomGetVM,
  IAddStudentToClassVM,
} from "app/dashboard/models/setup/general-setup/classes/class-room";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class ClassRoomService {
  constructor(private http: HttpClient) {}

  // Return All ClassRooms by page
  getAllClassRooms(
    page: number,
    eduCompId: number
  ): Observable<ClassRoomGetResponseVM> {
    return this.http.get<ClassRoomGetResponseVM>(
      `${environment.baseUrl}ClassRoom/getAllClassRoomByEduCompId/${eduCompId}/${page}`
    );
  }

  // Return All ClassRooms by page
  getAllClassRoomsDropDown(eduCompId: number): Observable<ClassRoomGetVM[]> {
    return this.http.get<ClassRoomGetVM[]>(
      `${environment.baseUrl}ClassRoom/getAllClassRoom/${eduCompId}`
    );
  }

  // Return All ClassRooms by Educational Year
  getAllClassRoomsByEducationalYearDropDown(
    eduCompId: number,
    EduYearId: number
  ) {
    return this.http.get<ClassRoomDropDownVM[]>(
      `${environment.baseUrl}AssignClassRoom/getAssignClassRoomByEduYearId/${EduYearId}/${eduCompId}`
    );
  }

  //Add New ClassRoom
  addUpdateClassRoom(data: ClassRoomAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}ClassRoom/addEditClassRoom`,
      data
    );
  }

  //Delete ClassRoom
  deleteClassRoom(id: number) {
    return this.http.delete(
      `${environment.baseUrl}ClassRoom/deleteClassRoom/${id}`
    );
  }

  /////// students in classroom
  //Add New ClassRoom
  assignStudentsToClassRoom(data: IAddStudentToClassVM) {
    return this.http.post(
      `${environment.baseUrl}ClassRoom/assignStudentsToClassRoom`,
      data
    );
  }

  //Delete ClassRoom
  deleteStudentFromClassRoom(id: number) {
    return this.http.delete(
      `${environment.baseUrl}ClassRoom/deleteStudentFromClassRoom/${id}`
    );
  }
}
