import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ClassRoomDropDownVM } from "app/dashboard/models/setup/general-setup/classes/class-room";
import { ClassRoomService } from "app/dashboard/services/setup/general-setup/classes/class-room.service";

@Component({
  selector: "app-class-room-by-educational-year-dropdown",
  templateUrl: "./class-room-by-educational-year-dropdown.component.html",
  styleUrls: ["./class-room-by-educational-year-dropdown.component.scss"],
})
export class ClassRoomByEducationalYearDropdownComponent implements OnInit {
  @Input() classRoomcontrol: FormControl;
  @Input() submitted: boolean;
  @Input() eduCompId: number;
  @Input() eduYearControl: FormControl;
  // show only classes with availble places for students || show all
  @Input() showAll: boolean = true;
  curLang: string;
  classRoomList: ClassRoomDropDownVM;

  constructor(private classRoomService: ClassRoomService) {}

  ngOnInit(): void {
    this.classRoomcontrol.disable();

    this.eduYearControl.valueChanges.subscribe((eduYearId) => {
      this.classRoomcontrol.setValue(null);
      if (eduYearId > 0) {
        this.getAllClassRoombyEduYear(eduYearId);
        this.classRoomcontrol.enable();
      } else {
        this.classRoomcontrol.disable();
      }
    });
  }

  getAllClassRoombyEduYear(eduYearId: number) {
    return this.classRoomService
      .getAllClassRoomsByEducationalYearDropDown(this.eduCompId, eduYearId)
      .subscribe((res: any) => {
        if (res) this.classRoomList = res.academicClassRoom;
      });
  }
}
