import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { IStageBodyVM } from "app/dashboard/models/setup/general-setup/stage";
@Injectable({
  providedIn: "root",
})
export class StagesService {
  constructor(private http: HttpClient) {}
  // Return All Stages Using Page
  getAllStages(page: number) {
    return this.http.get(`${environment.baseUrl}Stage/getStageByPage/${page}`);
  }

  // Return All Stages Dropdown
  getAllStagesDropdown() {
    return this.http.get(`${environment.baseUrl}Stage/getAllStage`);
  }

  //Add New Stage
  addUpdateStage(data: IStageBodyVM) {
    return this.http.post(`${environment.baseUrl}Stage/addEditStage`, data);
  }

  //Delete Stage
  deleteStage(StageId: number) {
    return this.http.delete(
      `${environment.baseUrl}Stage/deleteStage/${StageId}`
    );
  }
}
