import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService } from "app/dashboard/services/setup/student/application.service";
import { StudentService } from "app/dashboard/services/setup/student/student.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import {
  IStudentInfoAddEditVM,
  IBloodType,
  IStudentInfoGetVM,
  IStudentStatus,
} from "app/dashboard/models/setup/student/application";
import { MessagesService } from "app/shared/services/messages.service";
import { DatePipe, Location } from "@angular/common";

@Component({
  selector: "app-student-basic-info",
  templateUrl: "./student-basic-info.component.html",
  styleUrls: ["./student-basic-info.component.scss"],
})
export class StudentBasicInfoComponent implements OnInit {
  @Input("StudentID") StudentID: number = 0;
  @Input("isPreUniversityStudent") isPreUniversityStudent: boolean;
  // IsApplicant = true > new application || IsApplicant = false > new student
  @Input("IsApplicant") IsNewApplicant: boolean;
  @Output("chengeStudentId") chengeStudentId = new EventEmitter<number>();

  BloodTypes: IBloodType[];
  StudentStatusList: IStudentStatus[];
  StudentInfo: IStudentInfoGetVM;

  myForm: FormGroup;
  submitted: boolean = false;
  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private ApplicationServ: ApplicationService,
    private StudentServ: StudentService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private route: ActivatedRoute,
    private router: Router,
    private location: Location,
    public authserv: AuthService,
    private datePipe: DatePipe
  ) {
    // use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllBloodTypes();
    this.getAllStudentStatus();
    if (this.StudentID) this.getStudentInfoByStudentID();

    this.myForm = this.fb.group({
      // id: [this.StudentID],
      student_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      birthday: ["", [Validators.required]],
      edu_year_id: ["", [Validators.required]],
      status_id: ["", [Validators.required]],
      previous_school: ["", [Validators.required, CustomeValidator.whiteSpace]],
      address: ["", [Validators.required, CustomeValidator.whiteSpace]],
      blood_type_id: ["", [Validators.required]],
      student_mobile: [
        null,
        [
          Validators.required,
          CustomeValidator.whiteSpace,
          Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]*$/),
          // Validators.pattern(/^(010|011|012|015)[0-9]{8}$/),
          Validators.maxLength(11),
          Validators.minLength(11),
          CustomeValidator.startsWith,
        ],
      ],
      student_email: [null, [Validators.required, Validators.email]],
      // submitted: [false],
    });
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllBloodTypes();
      this.getAllStudentStatus();
      if (this.StudentID) this.getStudentInfoByStudentID();
    });
  }

  ngOnChanges(changes: any) {
    if (changes.isPreUniversityStudent?.currentValue == true) {
      this.formCtrls.student_mobile.clearValidators();
      this.formCtrls.student_mobile.updateValueAndValidity();
      this.formCtrls.student_email.clearValidators();
      this.formCtrls.student_email.updateValueAndValidity();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get blood types
  getAllBloodTypes() {
    this.ApplicationServ.getAllBloodTypes().subscribe((res) => {
      if (res) this.BloodTypes = res;
    });
  }

  // get blood types
  getAllStudentStatus() {
    this.ApplicationServ.getAllStudentStatus().subscribe((res) => {
      if (res) this.StudentStatusList = res.StatusList;
    });
  }

  // get student info if edit
  getStudentInfoByStudentID() {
    this.spinner.show();
    this.StudentServ.getApplicantStudentInfoById(this.StudentID).subscribe(
      (res) => {
        if (res) {
          this.StudentInfo = res;
          this.myForm.patchValue(res);
        }
        this.spinner.hide();
      }
    );
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let application: IStudentInfoAddEditVM = {
        edu_comp_id: this.EduCompId,
        id: this.StudentID,
        student_name: this.formCtrls.student_name.value,
        birthday: this.datePipe.transform(this.formCtrls.birthday.value),
        edu_year_id: this.formCtrls.edu_year_id.value,
        Status_Id: this.formCtrls.status_id.value,
        previous_school: this.formCtrls.previous_school.value,
        address: this.formCtrls.address.value,
        blood_type_id: this.formCtrls.blood_type_id.value,
        student_email: this.formCtrls.student_email.value,
        student_mobile: this.formCtrls.student_mobile.value,
      };
      // console.log("application", application);

      this.spinner.show();
      // if new application to add
      if (this.IsNewApplicant) this.addNewStudentApplication(application);
      // // if new student to add
      // else if (!this.IsNewApplicant) this.addNewStudent(application);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  addNewStudentApplication(application: IStudentInfoAddEditVM) {
    this.StudentServ.addEditApplicationStudentInfo(application).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          this.StudentID
            ? this.ToastrMsg.success("General.UpdateSuccessfully")
            : this.ToastrMsg.success("General.AddSuccessfully");
          this.submitted = false;
          this.StudentInfo = this.myForm.value;
          // get student id after add
          if (this.StudentID == 0)
            this.location.go(
              `dashboard/students/application/edit-application/${res.student_app[0].studentApp_id}?functionId=${this.functionId}`
            );
          this.StudentID = res.student_app[0].studentApp_id;
          this.chengeStudentId.emit(this.StudentID);
        } else this.ToastrMsg.error(res?.returnstring);
        this.spinner.hide();
      }
    );
  }

  resetForm() {
    this.myForm.reset();
    if (this.StudentID) this.myForm.patchValue(this.StudentInfo);
    this.submitted = false;
  }
}
